.displayValue{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: white;
    font-size: 26px;
    background-image: linear-gradient(#ffb70f 5%, #d6920f 50%, #ffb70f 95%);
    height: 40%;
    width: 90%;
    border-radius: 10px;
    box-shadow: inset 0px 0px 3px 1px #a46f0e;
}

.errorValue {
    color: red;
    font-weight: bold;
}